import * as d3 from 'd3'
import { zlCalculatorZl } from 'zl_calculator_zl'

const commonF = {
  // 获取实时时间
  getTime: () => {
    var strictIsoParse = d3.timeFormat('%m/%d/%Y-%H:%M:%S')
    return strictIsoParse(new Date())
  },
  // 可做数字（包含小数和整数）间的加，减，乘，除，括号，取余，等运算
  calculator: (CalString) => {
    const res = zlCalculatorZl(CalString)
    return res
  },
  // 绘制力导向图
  drawForceGraph: () => {
    var nodes = []
    var links = []
    var svgWidth = 400
    var svgHeight = 350

    function drawGraph (container) {
      setContainer(container)
      const simulations = simulation(nodes, links)
      const svg = d3.select(container).append('svg').attr('viewBox', [0, 0, svgWidth, svgHeight])
      const svg1 = transform(svg)
      svg1.append('marker')
        .attr('id', 'direction1')
        .attr('refX', 33) // 设置箭头离端点的距离
        .attr('refY', 0)
        .attr('orient', 'auto')
        .attr('stroke-width', 1)
        .attr('markerUnits', 'strokeWidth')
        .attr('markerUnits', 'userSpaceOnUse')
        .attr('viewBox', '0 -5 10 10')
        .attr('markerWidth', 6) // 设置箭头的大小
        .attr('markerHeight', 6) // 设置箭头的大小，长宽统一则不变形
        .append('path')
        .attr('d', 'M 0 -5 L 10 0 L 0 5')
        .attr('fill', 'black')
        .attr('stroke-opacity', 0.4)
      const g = svg1.append('g').attr('class', 'svgCon') // svg图的容器

      // 绘制图形

      const link = g.append('g').attr('class', 'linkG')
        .attr('stroke-opacity', 0.5)
        .attr('marker-end', 'url(#direction1)')
        .selectAll('path')
        .data(links)
        .join('path')
        .attr('stroke', function (d) {
          if (d.color !== undefined) {
            return d.color
          } else {
            return 'black'
          }
        })
        .attr('stroke-width', 1)
        .attr('class', 'link')
        .attr('id', d => d.source.id + '_' + d.target.id)

      const node = g.append('g').attr('class', 'nodeG')
        .attr('stroke', 'black')
        .attr('stroke-width', 1)
        .selectAll('circle')
        .data(nodes)
        .join('circle')
        .attr('r', 15)
        .attr('fill', '#FFA07A')
        .attr('class', function (d) {
          return d.id
        })
        .call(drag(simulations))
        .on('mouseover', function (d) {
          d3.select(this).attr('stroke-width', 4)
        })
        .on('mouseout', function (d) {
          d3.select(this).attr('stroke-width', 1)
        })

      node.append('title')
        .style('width', 19)
        .text(function (d) {
          return d.id
        })

      const nodeName = g.append('g').attr('class', 'nodeName')
        .selectAll('text')
        .data(nodes)
        .join('text')
        .attr('x', 10)
        .attr('y', 5)
        .attr('dx', 0)
        .attr('dy', 4)
        .style('font-weight', 'bold')
        .style('font-size', 9)
        .style('fill', 'black')
        .style('opacity', '0.8')
        .attr('text-anchor', 'middle')
        .attr('class', 'node_name')
        .text(function (d) {
          if (d.id.length > 2) {
            return d.id.slice(0, 2) + '...'
          } else {
            return d.id
          }
        })

      g.append('g').selectAll('text')
        .data(links)
        .join('text')
        .attr('dy', -4)
        .style('fill', 'black')
        .style('opacity', '0.5')
        .attr('text-anchor', 'middle')
        .attr('font-size', 9)
        .append('textPath')
        .attr('xlink:href', d => '#' + d.source.id + '_' + d.target.id)
        .attr('startOffset', '42%')
        .text(function (d) {
          return d.relation
        })
        .call(drag(simulation))

      simulations.on('tick', () => {
        node
          .attr('cx', d => d.x)
          .attr('cy', d => d.y)
        link
          .attr('d', d => 'M' + d.source.x + ' ' + d.source.y + 'L' + d.target.x + ' ' + d.target.y)
        nodeName
          .attr('x', d => d.x)
          .attr('y', d => d.y)
      })
      return drawGraph
    }
    drawGraph.addData = function (datas) {
      nodes = datas.nodes
      links = datas.links
      return drawGraph
    }// 添加原始数据

    var simulation = function (nodes, links) {
      const simu = d3.forceSimulation(nodes)
        .force('collision', d3.forceCollide(25))
        .force('link', d3.forceLink(links).id(d => d.id).distance(100))
        .force('charge', d3.forceManyBody().strength(-40))
      // .force('x',d3.forceX())
      // .force('y',d3.forceY())
        .force('redius', d3.forceRadial(svgWidth / 2, svgHeight / 2))
        .force('center', d3.forceCenter(svgWidth / 2, svgHeight / 2))
      return simu
    }
    var transform = function (svg) {
      svg.call(d3.zoom().on('zoom', function () {
        d3.select('.svgCon').attr('transform', d3.event.transform)
      })
      )
      return svg
    }
    var setContainer = function (container) {
      svgWidth = d3.select(container).node().getBoundingClientRect().width
      svgHeight = d3.select(container).node().getBoundingClientRect().height
    }
    var drag = function (simulation) {
      function dragstarted (d) {
        if (!d3.event.active) simulation.alphaTarget(0.3).restart()
        d.fx = d.x
        d.fy = d.y
      }

      function dragged (d) {
        d.fx = d3.event.x
        d.fy = d3.event.y
      }

      function dragended (d) {
        if (!d3.event.active) simulation.alphaTarget(0)
        d.fx = null
        d.fy = null
      }

      return d3.drag()
        .on('start', dragstarted)
        .on('drag', dragged)
        .on('end', dragended)
    }

    return drawGraph
  }
}

export default commonF
